<template>
  <div class="flex_wrapper">
    <header>
      <router-link to="/">
        <img src="../../static/top.png">
      </router-link>
    </header>
    <main>
      <transition-group tag="ul" name="panel" id="panel_list" appear>
        <li v-for="s in sephirah_list" :key="s.id">
          <transition name="fade">
          <div class="panel">
            {{ s.name }}
          </div>
          </transition>
        </li>
      </transition-group>
    </main>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  data () {
    return {
      sephirah_list: []
    }
  },
  methods: {
    get_sephirah () {
      const path = 'https://lobotomyinfostorehouse.herokuapp.com/api/get/sephirah'
      url= 'https://lobotomyinfostorehouse.herokuapp.com/api/get/sephirah'
      args={
        method: 'get',
        url: url + '?nocache=' + new Date().getTime(), // Safari fix
        withCredentials: true
      }
      axios.get(path)
        .then(response => {
          this.sephirah_list = response.data
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  created () {
    this.get_sephirah()
  }
}
</script>
<style lang="scss" scoped>
@import "https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.5.2/animate.css";
$header_height: 6rem;
$index_bg: #2b2b2b;
.flex_wrapper {
  display: flex;
  background-color: $index_bg;
  height: 100%;
  flex-direction: column;
}
.wrapper {
  display: grid;
  grid-template-rows: $header_height 1fr;
  grid-template-columns: 1fr;
  grid-template-areas: 'header' 'main-area';
  box-shadow: 0 0 10p rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background-color: $index_bg;
}
html {
  height: 100vh;
}
body {
  margin: 0;
  background-color: $index_bg;
}
header {
  grid-area: header;
  width: 100%;
    img{
      width: 100%;
    }
}
main {
  grid-area: main-area;
  width: 100%;
  height: 100%;
}
ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-auto-rows: 100px;
  padding-left: 1rem;
  padding-right: 1.5rem;
}
li {
  .panel {
    border: 1px solid #b8b641;
    color: #b8b641;
    box-shadow: 0px 0px 1px 1px #b8b641;
    height: 100%;
    font-size: 1rem;
    text-align: center;
    transition: 0.2s;
    &:active {
      box-shadow: 0px 0px 1px 3px #b8b641;
    }
  }
  display: block;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 2rem;
}
.container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.title {
  font-family: 'Quicksand', 'Source Sans Pro', -apple-system, BlinkMacSystemFont,
    'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  display: block;
  font-weight: 300;
  font-size: 100px;
  color: #35495e;
  letter-spacing: 1px;
}
.subtitle {
  font-weight: 300;
  font-size: 42px;
  color: #526488;
  word-spacing: 5px;
  padding-bottom: 15px;
}
.links {
  padding-top: 15px;
}
.panel-enter-active, .demo-leave-active {
  transition: transform .5s, opacity .5s;
}
.panel-enter {
  opacity: 0;
  transform: translateY(-20px);
}
</style>



